.post{
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
}
.post__top{
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
}
.post__avatar{
    margin-right: 10px;
}
.post__info>h3{
    font-size: medium;
}
.post__info>p{
    font-size: small;
    color: gray;
}
.post__image > img{
    width: 100%;
}
.post__bottom{
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 25px;
}
.post__options{
    display: flex;
    justify-content: space-evenly;
    padding: 15px;
    padding-top: 10px;
    color: gray;
    cursor: pointer;
    border-top: 1px solid lightgray; 
}
.post__option{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    flex: 1;
}
.post__option>p{
    margin-left: 10px;
}
.post__option:hover{
    background-color: #eff2f5;
    border-radius: 10px;
}